</template>

<script>
export default {
  name: 'AboutCard',
  props: {
    settings: {
      img: String,
      title: String,
      subtitle: String,
      skills: Array,
    },
    open: String,
  },
  methods: {
    handleOpen() {
      this.$emit('openInfo', this.$props.settings.img);
    },
  },
};
</script>

<style lang="scss">
.about-card {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  &__content {
    position: relative;
  }
  &__info {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #0181c8;
    border: 0;
    bottom: 22px;
    right: 30px;
    cursor: pointer;
    position: absolute;
    z-index: 1;
  }
  &::after {
    content: '';
    display: flex;
    width: 170px;
    height: 225px;
    border-radius: 15px;
    position: absolute;
    top: 16px;
    transform: rotate(15deg);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  }
}
.about-card-info {
  padding: 32px;
  border-radius: 5px;
  position: absolute;
  z-index: 3;
  border: 1px solid #d1d1d1;
  background-color: #fff;
  width: 304px;
  height: 265px;
  top: calc(100% + 4px);
  left: 50%;
  transform: translate(-50%);
  pointer-events: none;
  opacity: 0;
  transition: 0.2s ease-out;
  &__bg {
    opacity: 0.05;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  &__title {
    font-size: 20px;
    font-weight: 700;
  }
  &__subtitle {
    font-size: 14px;
    font-weight: 600;
    margin: 4px 0px 20px;
  }
  &--active {
    opacity: 1;
    .about-card-skill__progress-value {
      animation: load 1s ease-out forwards;
    }
  }
}
.about-card-skill {
  display: flex;
  gap: 8px;
  flex-direction: column;
  &__subtitle {
    font-size: 16px;
    font-weight: 600;
  }
  &__progress {
    box-shadow: 0px 2px 2px 0px #0000004d inset;
    height: 8px;
    width: 240px;
    background-color: #e0e0e0;
    box-sizing: border-box;
    border: 1px solid #ededed;
    border-radius: 5px;
    position: relative;
  }
  &__progress-value {
    position: absolute;
    top: 0;
    left: 0;
    height: 6px;
    background-color: #0181c8;
    border-radius: 5px;
    @keyframes load {
      0% {
        width: 0;
      }
      100% {
        width: var(--progress);
      }
    }
  }
  & + & {
    margin-top: 16px;
  }
}
@media (max-width: 1200px) {
  .about-card {
    &__img {
      width: 162px;
      height: 192px;
    }
    &::after {
      width: 128px;
      height: 170px;
      top: 10px;
    }
    &__info {
      width: 30px;
      height: 30px;
      right: 20px;
      bottom: 18px;
      svg {
        width: 7.41px;
        height: 18.62px;
      }
    }
  }
}
@media (max-width: 768px) {
  .about-card {
    &__img {
      width: 215px;
      height: 255px;
    }
    &::after {
      width: 170px;
      height: 225px;
      top: 16px;
    }
    &__info {
      width: 40px;
      height: 40px;
      background-color: #0181c8;
      bottom: 22px;
      right: 30px;
      svg {
        width: 10px;
        height: 25px;
      }
    }
  }
  .about-card {
    &:nth-child(odd) {
      .about-card-info {
        left: 100%;
        transform: translate(-48%);
      }
    }
    &:nth-child(even) {
      .about-card-info {
        right: 100%;
        left: auto;
        transform: translate(48%);
      }
    }
  }
}
@media (max-width: 570px) {
  .about-card {
    &__info {
      width: 33px;
      height: 33px;
      right: 13px;
      svg {
        width: 8px;
        height: 20px;
      }
    }
    &__img {
      width: 145px;
      height: 172px;
    }
    &::after {
      width: 114px;
      height: 152px;
      top: 10px;
    }
  }
}
</style>
